.navigation {
  visibility: hidden;
  opacity: 0;
  &_active {
    padding: 70px 55px 30px 55px;
    top: 0;
    right: 0;
    width: 415px;
    // bottom: 0;
    // left: 0;
    // width: 100%;
    position: fixed;
    display: flex;
    // justify-content: space-between;
    flex-direction: column;
    background: #f4f4f4;
    z-index: 1;
    // overflow: auto;
    box-sizing: border-box;
    visibility: visible;
    opacity: 1;
    transition: visibility 0.2s, opacity 0.2s linear;
  }
}
